:host {
  display: block;
  box-sizing: border-box;
}

button {
  all: unset;
  border-radius: var(--radius, 8px);
  color: white;
  font-weight: 500;
  cursor: pointer;
  gap: 8px;
  transition: all 0.3s ease-in-out;
  width: var(--ir-btn-width, inherit);
  box-sizing: border-box;
}

button:disabled {
  user-select: none;
  pointer-events: none;
}

button:focus-visible,
button:active {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 4px hsla(var(--brand-100, 209, 100%, 91%), 0.2);
}

.button-default {
  background: hsla(var(--brand-600), 1);
}
.button-default:hover {
  background: hsl(var(--brand-700, 218, 80%, 46%));
}
.button-default:disabled {
  background: hsl(var(--brand-200, 206, 100%, 85%));
}

.button-secondary,
.button-secondary:active,
.button-secondary:focus {
  background: hsl(var(--brand-50, 206, 100%, 97%));
  color: hsl(var(--brand-700, 218, 80%, 46%));
}
.button-secondary .loader {
  background-color: hsl(var(--brand-700, 218, 80%, 46%));
}
.button-secondary:hover {
  background: hsl(var(--brand-100, 209, 100%, 91%));
}
.button-secondary:disabled {
  color: hsl(var(--brand-300, 206, 100%, 76%));
}
.button-default:disabled::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: hsl(var(--brand-50, 206, 100%, 97%));
  opacity: 0.5;
  z-index: -1;
}

.button-outline {
  border: 1px solid var(--gray-300, #d0d5dd);
  background: white;
  color: var(--gray-700, #344054);
}
.button-outline .loader {
  background-color: var(--gray-700, #344054);
}
.button-outline:hover {
  background: var(--gray-50, #f9fafb);
  color: var(--gray-800, #1d2939);
}
.button-outline:disabled {
  background: var(--gray-200, #eaecf0);
  color: var(--gray-300, #d0d5dd);
}

.button-outline:focus-visible,
.button-outline:active {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 4px var(--gray-100, #f2f4f7);
}
.button-outline-primary {
  border: 1px solid hsl(var(--brand-600, #d0d5dd));
  color: hsl(var(--brand-700, #344054));
}
.button-outline-primary .loader {
  background-color: hsl(var(--gray-700, #344054));
}
.button-outline-primary:hover {
  background: hsla(var(--brand-50, #f9fafb), 0.05);
  color: hsl(var(--brand-800, #1d2939));
}
.button-outline-primary:disabled {
  background: hsl(var(--brand-200, #eaecf0));
  color: hsl(var(--brand-300, #d0d5dd));
}

.button-outline-primary:focus-visible,
.button-outline-primary:active {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 4px hsl(var(--brand-100, #f2f4f7), 0.1);
}

.button-ghost,
.button-icon {
  color: var(--gray-600, #475467);
}
.button-ghost:hover,
.button-icon:hover {
  background: var(--gray-100, #fcfcfd);
  color: var(--gray-700, #344054);
}

.button-ghost:disabled,
.button-icon:disabled {
  color: var(--gray-300, #d0d5dd);
}

.button-ghost-primary {
  background: white;
  color: hsl(var(--brand-600, 215, 87%, 51%));
}
.button-ghost-primary:hover {
  background: hsla(var(--brand-50, 206, 100%, 97%), 0.2);
  color: hsl(var(--brand-700, 218, 80%, 46%));
}

.button-ghost-primary:disabled {
  background: white;
  color: var(--brand-300, #d0d5dd);
}

.button-link {
  background: white;
  color: var(--gray-600, #475467);
}
.button-link:hover {
  text-decoration: underline;
}
.button-link:disabled {
  background: white;
  color: var(--gray-300, #d0d5dd);
}

.button-ghost:focus-visible,
.button-ghost:active,
.button-ghost-primary:focus-visible,
.button-ghost-primary:active,
.button-icon:focus-visible,
.button-icon:active,
.button-link:focus-visible,
.button-link:active {
  box-shadow: none;
}

.button-destructive {
  background: var(--error-600, #d92d20);
}
.button-destructive:hover {
  background: var(--error-700, #b42318);
}
.button-destructive:disabled {
  background: var(--error-200, #fecdca);
}

.button-destructive:focus-visible,
.button-destructive:active {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 4px var(--error-100, #fee4e2);
}

/* Size */

button[data-size='sm'] {
  padding: 8px 14px;
  font-size: 14px;
  line-height: 14px;
}
button[data-size='md'] {
  padding: 8px 16px;
  font-size: 16px;
  line-height: 24px;
}
button[data-size='lg'] {
  padding: 11px 20px;
  font-size: 16px;
  line-height: 24px;
}

.button-icon[data-size='sm'] {
  padding: 8px;
}
.button-icon[data-size='md'] {
  height: 3rem;
  padding: 10px;
}
.button-icon[data-size='lg'] {
  padding: 12px;
}

.loader {
  width: 12px;
  padding: 2px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: white;
  --_m: conic-gradient(#0000 10%, #000), linear-gradient(#000 0 0) content-box;
  -webkit-mask: var(--_m);
  mask: var(--_m);
  -webkit-mask-composite: source-out;
  mask-composite: subtract;
  animation: l3 1s infinite linear;
}
@keyframes l3 {
  to {
    transform: rotate(1turn);
  }
}
